.ChatArea {
    grid-row: 2;
    grid-column: 2;
    color: black;
    padding-left: 20px;
    background: white;
    -webkit-box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.3);
            box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.3);

    overflow-y: scroll;
    max-height: calc(100vh - var(--nav-height) - var(--chat-height));
}



