.Button {
  padding: 20px;
  display: inline-block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: black;
  font-size: 20pt;
  font-family: sans-serif;
  margin-left: 5px;
  border: 1px solid gray;
  border-radius: 40px;
  -webkit-transition: 0.1s;
  -o-transition: 0.1s;
  transition: 0.1s;
  -webkit-box-shadow: inset -3px -3px 1px 1px rgba(100, 100, 100, 0.3);
          box-shadow: inset -3px -3px 1px 1px rgba(100, 100, 100, 0.3);
  cursor: pointer;
  min-width: 50px;
  padding-top: 20px;
  text-align: center;
  background: rgba(255, 255, 255, 0.8);
  height: 70px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin-top: 2px;
}

.Button:active {
  -webkit-box-shadow: inset 3px 3px 1px 1px rgba(100, 100, 100, 0.3);
          box-shadow: inset 3px 3px 1px 1px rgba(100, 100, 100, 0.3);
  padding-top: 23px;
}




